.footer {
  font-size: 16px;
  position: relative;
  background: #FCBC1D; }
  .footer__address {
    font-size: 14px;
    line-height: 20px; }
  .footer__title, .footer__copyright {
    font-size: 16px;
    font-weight: 800;
    margin-bottom: 8px; }
  .footer__separeteLine {
    background: #161620;
    height: 1px; }
  .footer__social, .footer__payment {
    margin-right: 8px; }
  .footer__link {
    display: block;
    cursor: pointer;
    margin-bottom: 8px; }
  .footer__line {
    padding: 32px 0; }
  .footer__icon {
    position: absolute;
    left: 20px;
    bottom: 0px; }

.contactFooter {
  text-align: center; }
  .contactFooter__form {
    padding-right: 0.8rem;
    margin-bottom: 2.5rem; }
  .contactFooter__emails {
    padding-left: 0.8rem;
    margin-bottom: 2.5rem; }
  .contactFooter__title {
    display: block;
    font-weight: 600;
    letter-spacing: -.02em;
    line-height: 1.1;
    font-size: 2.6rem; }
  .contactFooter__text {
    font-size: 1rem; }
  .contactFooter__subtitle {
    color: #272727;
    font-size: .8rem;
    letter-spacing: .015em;
    text-transform: uppercase; }
  .contactFooter__email {
    display: block;
    font-weight: 600;
    letter-spacing: -.02em;
    line-height: 1.2;
    font-size: 1.4rem; }
