.breadcrumbs {
  margin-right: 25px; }

.header {
  background-color: #fff;
  border-bottom: 1px solid rgba(0, 0, 0, 0.0975); }
  .header__submenu {
    vertical-align: middle;
    margin-top: 3px; }
  .header__submenuMenu {
    display: inline-block;
    font-size: 14px;
    color: #4b5761;
    text-transform: uppercase;
    margin: 0 16px;
    position: relative;
    cursor: pointer; }
    .header__submenuMenu.isActive, .header__submenuMenu:hover {
      color: #4b5761;
      text-decoration: none; }
      .header__submenuMenu.isActive:before, .header__submenuMenu.isActive:before, .header__submenuMenu:hover:before, .header__submenuMenu:hover:before {
        right: auto;
        left: 0;
        width: 100%;
        display: block;
        content: ""; }
    .header__submenuMenu span {
      max-width: 140px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      display: block; }
    .header__submenuMenu.isActive {
      font-weight: 700; }
    .header__submenuMenu:first-child {
      margin-left: 0; }
    .header__submenuMenu:before {
      position: absolute;
      content: "";
      width: 0;
      right: 0;
      left: auto;
      bottom: -7px;
      height: 2px;
      background: #4a4a4a;
      display: block;
      transition: all .2s ease; }
  .header__wrapper {
    align-items: center;
    height: 77px; }
  .header__logo {
    display: inline-block;
    background-image: url("https://www.instagram.com/static/bundles/base/sprite_glyphs_2x.png/5645ce9a6227.png");
    background-size: 241px 213px;
    background-position: -50px -164px;
    background-repeat: no-repeat;
    height: 24px;
    width: 24px; }
  .header__separator {
    display: inline-block;
    background-color: #262626;
    height: 28px;
    margin: 0 16px;
    width: 1px; }
  .header__title {
    font-size: 18px;
    font-weight: 600;
    color: #252729; }
