.btn {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  box-sizing: border-box;
  font-size: 16px;
  font-weight: 600;
  color: #161620;
  white-space: nowrap;
  cursor: pointer;
  border: none;
  outline: none;
  border-radius: 4px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  text-align: center;
  background-color: transparent;
  padding: 0;
  box-shadow: none;
  transition: color .35s; }

@media (min-width: 668px) {
  .btn {
    min-width: 240px; } }

@media (min-width: 668px) and (max-width: 1440px) {
  .btn {
    min-width: 216px; } }

@media (min-width: 668px) and (max-width: 1200px) {
  .btn {
    min-width: 192px; } }

@media (min-width: 668px) and (max-width: 992px) {
  .btn {
    min-width: 127px; } }

.btn--hover,
.btn:hover {
  background-color: transparent;
  color: #161620; }

.btn--hover .btn__inner,
.btn:hover .btn__inner {
  background-color: #be6d00;
  transition-duration: .175s; }

.btn:disabled {
  cursor: not-allowed;
  color: #a3a5b1; }

.btn--hover.btn:disabled .btn__inner,
.btn:disabled .btn__inner,
.btn:disabled:hover .btn__inner {
  background-color: #be6d00; }

.btn__inner {
  position: relative;
  display: block;
  box-sizing: border-box;
  padding: 18px 24px;
  border-radius: 4px;
  background-color: #FCBC1D;
  -webkit-transition: background-color .35s, -webkit-transform .35s, -webkit-box-shadow .35s;
  transition: background-color .35s, -webkit-transform .35s, -webkit-box-shadow .35s;
  -o-transition: background-color .35s, transform .35s, box-shadow .35s;
  transition: background-color .35s, transform .35s, box-shadow .35s;
  transition: background-color .35s, transform .35s, box-shadow .35s, -webkit-transform .35s, -webkit-box-shadow .35s;
  z-index: 1; }

@media (max-width: 1440px) {
  .btn__inner {
    padding: 18px 32px; } }

.btn:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 4px;
  -webkit-box-shadow: 0 2px 4px #06f;
  box-shadow: 0 2px 4px #06f;
  -webkit-transform-origin: 50% 100%;
  -ms-transform-origin: 50% 100%;
  transform-origin: 50% 100%;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  opacity: .3;
  -webkit-transition: opacity .35s, -webkit-transform .35s;
  transition: opacity .35s, -webkit-transform .35s;
  -o-transition: transform .35s, opacity .35s;
  transition: transform .35s, opacity .35s;
  transition: transform .35s, opacity .35s, -webkit-transform .35s; }

.btn-wrapper {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  -webkit-box-shadow: 0 2px 4px #cdd0d3;
  box-shadow: 0 2px 4px #cdd0d3;
  border-radius: 4px; }

.btn__arrow {
  position: absolute;
  top: 50%;
  margin-top: -10px;
  right: 22px;
  fill: currentColor;
  -webkit-transition: -webkit-transform .35s;
  transition: -webkit-transform .35s;
  -o-transition: transform .35s;
  transition: transform .35s;
  transition: transform .35s, -webkit-transform .35s;
  -webkit-transition-timing-function: cubic-bezier(0.445, 0.05, 0.55, 0.95);
  -o-transition-timing-function: cubic-bezier(0.445, 0.05, 0.55, 0.95);
  transition-timing-function: cubic-bezier(0.445, 0.05, 0.55, 0.95);
  width: 34px; }

.btn__text {
  display: inline-block;
  -webkit-transition: -webkit-transform 0.35s cubic-bezier(0.77, 0, 0.175, 1);
  transition: -webkit-transform 0.35s cubic-bezier(0.77, 0, 0.175, 1);
  -o-transition: transform 0.35s cubic-bezier(0.77, 0, 0.175, 1);
  transition: transform 0.35s cubic-bezier(0.77, 0, 0.175, 1);
  transition: transform 0.35s cubic-bezier(0.77, 0, 0.175, 1), -webkit-transform 0.35s cubic-bezier(0.77, 0, 0.175, 1);
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-transform-origin: center;
  -ms-transform-origin: center;
  transform-origin: center; }

.btn .Link__target-blank {
  display: none; }

.btn--arrow {
  text-align: left; }

.btn--arrow .btn__inner {
  background-color: transparent;
  padding-right: 80px; }

@media (min-width: 668px) {
